import React from 'react';
import { connect } from 'react-redux';
import { Navigate, navigate } from '../components/Navigate';
import { Table, TableRow, TableLoading, TableCol } from '../components/Table';
import * as actions from '../actions/posts';
import { Row, Col } from '../components/Col';

class PostList extends React.Component {
  static propTypes = {
    posts: React.PropTypes.array
  };

  constructor() {
    super();
  }

  componentDidMount() {
    this.props.fetchPostsIfNeeded(this.props.params.postType);
  }

  componentWillReceiveProps(newProps) {
    if (newProps.params.postType !== this.props.params.postType) {
      this.props.fetchPostsIfNeeded(newProps.params.postType);
    }
  }

  renderPosts() {
    if (this.props.posts) {
      return this.props.posts.map((post, index) => {
        return (
          <TableRow key={index}>
            <TableCol>
              <Navigate path={`posts/${this.props.params.postType}/${post.slug}`}>{post.title}</Navigate>
            </TableCol>

            <TableCol>
            <div className="btn-group btn-group-sm pull-xs-right" role="group" aria-label="Basic example">
              <Navigate
                path={`posts/${this.props.params.postType}/${post.slug}`}
                className="btn btn-secondary"
              >
                Edit
              </Navigate>
              <Navigate
                path={`posts/${this.props.params.postType}/${post.slug}`}
                className="btn btn-danger"
              >
                Delete
              </Navigate>
            </div>
            </TableCol>
          </TableRow>
        );
      });
    }

    return null;
  }

  render() {
    return (
      <div>
        <Row>
          <Col width="sm-10">
            <h2>{this.props.postType.labels.plural}</h2>
          </Col>
          <Col width="sm-2">
            <button type="button" className="btn btn-outline-primary pull-xs-right">Create new Post</button>
          </Col>
        </Row>
        <Row>
          <Col width='sm-12'>
            <Table headers={['Name', 'Action']}>
              <TableLoading loading={this.props.loading} />
              {this.renderPosts()}
            </Table>
          </Col>
        </Row>
      </div>
    )
  }
}


const mapStateToProps = (state, props) => {
  return {
    posts: state.posts.list[props.params.postType],
    loading: state.posts.loading >= 1,
    postType: state.postTypes[props.params.postType]
  };
};

const mapDispatchToProps = (dispatch, props) => {

  return {
    fetchPostsIfNeeded: (postType) => {
      dispatch(actions.fetchPostsIfNeeded(postType));
    }
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(PostList);



