import React from 'react';
import { connect } from 'react-redux';

const GlobalLoader = ({ loading, children }) => {
  if (loading >= 1) {
    return (
      <div className='global-loader'>Loading...</div>
    );
  } else if (loading < 0) {
    return (
      <div>Error loading dashboard.</div>
    );
  }

  return (
    <div>{children}</div>
  );
};

export default connect((state) => ({
  loading: state.loadingRequired
}))(GlobalLoader);



