import React from 'react';
import { connect } from 'react-redux';
// import { List, ListItem } from '../components/List';

const PostTypeList = ({ postTypes }) => (
  <div>
    <h2>Post Types</h2>
    {/* <List>
      {Object.keys(postTypes).map((typeKey, index) => {
        const type = postTypes[typeKey];
        return (
          <ListItem key={index} title={type.name} path={'posts/' + type.slug} />
        );
      })}
    </List> */}
  </div>
)

const mapStateToProps = state => ({
  postTypes: state.postTypes
});

export default connect(mapStateToProps)(PostTypeList);



