import React from 'react';
import { Navigate } from '../components/Navigate';

const NavBar = ({ postTypes }) => {
  return (
    <nav className="navbar navbar-dark bg-primary">
      <button className="navbar-toggler hidden-sm-up" type="button" data-toggle="collapse" data-target="#exCollapsingNavbar2" aria-controls="exCollapsingNavbar2" aria-expanded="false" aria-label="Toggle navigation">
        &#9776;
      </button>
      <div className="collapse navbar-toggleable-xs" id="exCollapsingNavbar2">
        <a className="navbar-brand" href="#">Polar CMS</a>
        <ul className="nav navbar-nav">
          <li className="nav-item active">
            <a className="nav-link" href="#">Home <span className="sr-only">(current)</span></a>
          </li>
          <li className="nav-item dropdown">
            <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">Posts</a>
            <div className="dropdown-menu">
              {Object.keys(postTypes).map((typeKey, index) => {
                const type = postTypes[typeKey];
                return <Navigate key={index} path={`/posts/${type.slug}`} className="dropdown-item">{type.name}</Navigate>;
              })}
            </div>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#">Features</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#">Pricing</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#">About</a>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default NavBar;

